import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../components/Layout';
import Container from '../components/Container';
import BlockCTA from '../components/BlockCTA';
import SpaceDivider from '../components/SpaceDivider';
import Footer from '../components/Footer';

import { colors, fonts } from '../constants';
import { formatDate } from '../utils/date';

export const pageQuery = graphql`
  query PostBySlug($slug: String!) {
    post: contentfulBlogPost(slug: { eq: $slug }) {
      title
      description
      slug
      tag
      feature_image {
        fluid(maxHeight: 450) {
          ...GatsbyContentfulFluid
        }
      }
      html {
        childContentfulRichText {
          html
        }
      }
      body {
        childMarkdownRemark {
          html
        }
      }
      summary
      summary_long
    }
    site {
      siteMetadata {
        siteUrl
      }
    }
  }
`;

export default function PostPage({ data }) {
  const { post } = data;

  return (
    <Layout ogType="article">
      <GatsbySeo
        title={post.title}
        description={post.description}
        openGraph={{
          url: `${data.site.siteMetadata.siteUrl}/${post.slug}/`,
          title: post.title,
          description: post.description,
          type: 'article',
        }}
      />
      <Container>
        <article className="blog-post">
          <header className="blog-post-header">
            <section className="blog-post-meta">
              {post.tag && (
                <>
                  <span className="blog-post-meta-tag">{post.tag}</span>
                </>
              )}
            </section>
            <h1 className="blog-post-title">{post.title}</h1>
          </header>
          <Img
            fluid={post.feature_image.fluid}
            className="blog-post-image"
            imgStyle={{ objectFit: 'cover', objectPosition: 'top center' }}
          />
          <section className="blog-post-content">
            <div
              dangerouslySetInnerHTML={{
                __html: post.body?.childMarkdownRemark?.html
                 || post.html.childContentfulRichText.html,
              }}
            />
          </section>
        </article>
        <SpaceDivider height={100} />
        <BlockCTA
          pretitle="Net terms & credit management"
          title="Ready to offer smarter credit with Resolve?"
          primaryButtonTo="/"
          primaryButtonText="Learn more"
          align="center"
        />
      </Container>
      <Footer darkSkin />
      <style jsx global>{`
        b,
        strong {
          font-weight: bold;
        }

        .blog-post-header {
          margin: 0 auto;
          padding: 6vw 3vw 48px;
          max-width: 1040px;
          text-align: center;
        }

        .blog-post-meta {
          display: flex;
          justify-content: center;
          align-items: center;
          color: #738a94;
          font-size: 0.9rem;
          letter-spacing: 0.2px;
          font-weight: 400;
          text-transform: uppercase;
        }

        .blog-post-meta .divider {
          display: inline-block;
          margin: 0 6px 1px;
        }

        .blog-post-meta-date {
          color: #738a94;
        }

        .blog-post-title {
          color: $dark-slate-blue;
          font-family: ${fonts.halyardDisplay};
          font-weight: 600;
          font-size: 3rem;
          line-height: 1.2;
          margin: 0.75rem 0 0 0;
        }

        .blog-post-image {
          background-size: cover;
          max-height: 350px;
          max-width: 900px;
          margin: 0 auto;
        }

        .blog-post-content {
          position: relative;
          margin: 0 auto;
          padding: 70px 180px 0;
          min-height: 230px;
          font-family: ${fonts.blink};
          font-size: 1.3rem;
          line-height: 1.6em;
          background: #fff;
        }

        .blog-post-content * {
          box-sizing: border-box;
        }

        .blog-post-content a {
          text-underline-position: under;
        }

        .kg-card-markdown {
          color: ${colors.darkSlateBlue};
          display: flex;
          flex-direction: column;
          align-items: center;
          max-width: 820px;
          margin: 0 auto;
        }

        .blog-post-content h1,
        .blog-post-content h2,
        .blog-post-content h3,
        .blog-post-content h4,
        .blog-post-content h5,
        .blog-post-content h6 {
          line-height: 130%;
        }

        .blog-post-content h1,
        .blog-post-content h2,
        .blog-post-content h3,
        .blog-post-content h4,
        .blog-post-content h5,
        .blog-post-content h6,
        .blog-post-content p,
        .blog-post-content ul,
        .blog-post-content ol,
        .blog-post-content dl,
        .blog-post-content pre,
        .blog-post-content blockquote,
        .blog-post-comments,
        .footnotes {
          min-width: 100%;
        }

        .blog-post-content li {
          word-break: break-word;
          padding-left: 0.75rem;
        }

        .blog-post-content p {
          margin-bottom: 1rem;
        }

        .blog-post-content li p {
          margin: 0;
        }

        .kg-card-markdown > p:first-child {
          font-size: 1.25em;
          line-height: 1.5em;
        }

        .blog-post-content a {
          color: lighten(${colors.salmon}, 5%);
          text-decoration: underline;
        }

        .blog-post-content a:hover {
          text-decoration: none;
        }

        .blog-post-content strong,
        .blog-post-content em {
          color: color(${colors.darkSlateBlue}} l(-5%));
        }

        .blog-post-content small {
          display: inline-block;
          line-height: 1.6em;
        }

        .blog-post-content li:first-child {
          margin-top: 0;
        }

        .blog-post-content img,
        .blog-post-content video {
          display: block;
          margin: 1.5em auto;
          max-width: 100%;
        }
        @media (max-width: 1040px) {
          .blog-post-content img,
          .blog-post-content video {
            width: 100%;
          }

          .blog-post-content {
            padding: 35px 35px 0;
          }
        }

        .blog-post-content img[src$='#full'] {
          max-width: none;
          width: 100vw;
        }

        .blog-post-content img + br + small {
          display: block;
          margin-top: -3em;
          margin-bottom: 1.5em;
        }

        .blog-post-content iframe {
          margin: 0 auto;
        }

        .blog-post-content blockquote {
          background: rgba(0, 0, 0, 0.015);
          border: lighten(${colors.darkSlateBlue}, 65%) 1px solid;
          border-left: lighten($dark-slate-blue, 35%) 4px solid;
          margin: 0 0 1.5rem;
          padding: 1.3rem 1.5rem 1.4rem 1.5rem;
        }

        .blog-post-content blockquote p strong {
          font-weight: bold;
        }

        .blog-post-content blockquote p {
          margin: 0 0 1em 0;
          color: ${colors.darkSlateBlue};
          font-size: inherit;
          line-height: inherit;
          font-style: italic;
        }

        .blog-post-content blockquote p:last-child {
          margin-bottom: 0;
        }

        .blog-post-content code {
          padding: 0 5px 2px;
          font-size: 0.8em;
          line-height: 1em;
          font-weight: 400 !important;
          background: var(--whitegrey);
          border-radius: 3px;
        }

        .blog-post-content pre {
          overflow-x: auto;
          margin: 1.5em 0 3em;
          padding: 20px;
          max-width: 100%;
          border: color(var(--darkgrey) l(-10%)) 1px solid;
          color: var(--whitegrey);
          font-size: 1.4rem;
          line-height: 1.5em;
          background: color(var(--darkgrey) l(-3%));
          border-radius: 5px;
        }

        .blog-post-content pre code {
          padding: 0;
          font-size: inherit;
          line-height: inherit;
          background: transparent;
        }

        .blog-post-content pre code * {
          color: inherit;
        }

        .blog-post-content .fluid-width-video-wrapper {
          margin: 1.5em 0 3em;
        }

        .blog-post-content hr {
          margin: 4vw 0;
        }

        .blog-post-content hr:after {
          content: '';
          position: absolute;
          top: -15px;
          left: 50%;
          display: block;
          margin-left: -10px;
          width: 1px;
          height: 30px;
          background: color(var(--lightgrey) l(+10%));
          box-shadow: #fff 0 0 0 5px;
          transform: rotate(45deg);
        }

        .blog-post-content h1,
        .blog-post-content h2,
        .blog-post-content h3,
        .blog-post-content h4,
        .blog-post-content h5,
        .blog-post-content h6 {
          color: color(var(--darkgrey) l(-5%));
          font-family: ${fonts.halyardDisplay};
        }

        .blog-post-content h1 {
          margin: 1.2em 0 0.6em 0;
          font-size: 3.25rem;
          font-weight: 600;
        }
        @media (max-width: 500px) {
          .blog-post-content h1 {
            font-size: 2.6rem;
          }
        }

        .blog-post-content h2 {
          margin: 1.2em 0 0.6em 0;
          font-size: 1.75rem;
          font-weight: 600;
        }

        .blog-post-content h3 {
          margin: 1.2em 0 0.3em 0;
          font-size: 1.3rem;
          font-weight: 600;
        }

        .blog-post-content h4 {
          margin: 0.5em 0 0.2em 0;
          font-size: 1.3rem;
          font-weight: 500;
        }

        .blog-post-content h5 {
          margin: 0.5em 0;
          font-size: 1.3rem;
          font-weight: 500;
        }
        @media (max-width: 500px) {
          .blog-post-content h5 {
            padding: 0 0 0.5em;
          }
        }

        .blog-post-content h6 {
          margin: 0.5em 0 0.2em 0;
          font-size: 1.3rem;
          font-weight: 500;
        }

        .footnotes-sep {
          margin-bottom: 30px;
        }

        .footnotes {
          font-size: 1.5rem;
        }

        .footnotes p {
          margin: 0;
        }

        .footnote-backref {
          color: var(--blue) !important;
          font-size: 1.2rem;
          font-weight: bold;
          text-decoration: none !important;
          box-shadow: none !important;
        }

        @media (max-width: 800px) {
          .blog-post-title {
            font-size: 2.4rem;
          }
        }

        /* Some grouped styles for smaller viewports */
        @media (max-width: 500px) {
          .blog-post-header {
            padding-left: 0;
            padding-right: 0;
            padding-bottom: 32px;
          }
          .blog-post-title {
            font-size: 1.8rem;
            text-align: left;
          }
          .blog-post-image {
            margin-bottom: 4vw;
            height: 350px;
            display: none;
          }
          .blog-post-content {
            font-size: 1.1rem;
            padding: 0;
          }
          .blog-post-content:before,
          .blog-post-content:after {
            display: none;
          }
          .blog-post-content h2 {
            font-size: 1.5rem;
          }
          .blog-post-content ul,
          .blog-post-content ol {
            padding-inline-start: 32px;
          }
        }

        /* Tables */
        .blog-post-content table {
          display: inline-block;
          overflow-x: auto;
          margin: 0.5em 0 2.5em;
          max-width: 100%;
          width: auto;
          border-spacing: 0;
          border: solid 1px rgba(0, 0, 0, 0.2);
          border-collapse: collapse;
          font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
            Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
          white-space: nowrap;
          vertical-align: top;
        }

        .blog-post-content table {
          -webkit-overflow-scrolling: touch;
          background-attachment: scroll, scroll;
          background-size: 10px 100%, 10px 100%;
          background-repeat: no-repeat;
        }

        .blog-post-content table td:first-child {
          background-size: 20px 100%;
          background-repeat: no-repeat;
        }

        .blog-post-content table td:last-child {
          background-position: 100% 0;
          background-size: 20px 100%;
          background-repeat: no-repeat;
        }

        .blog-post-content table th {
          color: var(--darkgrey);
          font-size: 1.2rem;
          font-weight: 600;
          letter-spacing: 0.2px;
          text-align: left;
          text-transform: uppercase;
          background-color: color(var(--whitegrey) l(+4%));
        }

        .blog-post-content table th,
        .blog-post-content table td {
          padding: 6px 12px;
          border: solid 1px rgba(0, 0, 0, 0.1);
        }
      `}</style>
    </Layout>
  );
}
